.page-9 {
  position: relative;
  .page--main-header {
    font-size: 28px;
    margin-top: 50px;
  }
  ul {
    padding-left: 100px;
    color: $mint;
    margin-bottom: 30px;
    li {
      margin-bottom: 8px;
      font-weight: 500;
    }
  }
  &__demo {
    font-size: 20px;
    text-align: center;
    font-weight: 700;
    a {
      text-decoration: underline;
      color: black;
    }
  }
  &__image {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 55%;
    img {
      width: 100%;
      object-fit: cover;
      height: 100%;
    }
  }
}
